.has-shop-count {
    i[data-shop-count="0"] {
        &::before {
            display: none;
        }
    }
    i {
        vertical-align: top;
        font-style: normal;
        &::before {
            //
            box-sizing: content-box;
            //http://www.cnblogs.com/zjzhome/p/4898898.html
            //Web
            //https://www.zhihu.com/question/21093147?rf=21339583
            //WebKit  -webkit-text-size-adjust 
            // Chrome  12px 
            -webkit-text-size-adjust: none;
            //
            //display: block;
            //http://www.jb51.net/article/81984.htm
            //dataset
            position: absolute;
            display: block;
            content: attr(data-shop-count);

            //z-index: @z-index-nav-search-bar + 1;
            right: calc(~"25% - 4px");
            top: -1px;
            height: 16px;
            //
            width: 15px;

            font-size: 12px;
            line-height: 1.4;
            transform: scale(0.95);
            letter-spacing: -1px;
            vertical-align: top;
            //
            padding: 0 1px 0 0;

            color: #fff;
            background-color: @red;
            box-shadow:  0 0 0px 1px rgba(255,255,255,1);
            border-radius: 50%;

            //6p 
            @media screen and (min-width: 414px) {
                right: calc(~"28% - 4px");
            }
        }
    }
}


