.change-user-info-close {
    position: absolute;
    top: unit(5/@base-px-2-rem, rem);
    right: unit(5/@base-px-2-rem, rem);
    width: unit(1/@base-px-2-rem, rem);
    height: unit(1/@base-px-2-rem, rem);
    padding: unit(50/@base-px-2-rem, rem);
    &:after, &:before {
        content: "";
        position: relative;
        width: 23px;
        height: 3px;
        background-color: #ccc;
        display: block;
        left: -12px;
    }
    &:after {
        margin-top: -3px;
        transform: rotate(45deg);
    }
    &:before {
        transform: rotate(135deg);
    }
}