************************/


/************************

 - 

************************/

section.product-more {
    background-color: #fff;
    padding-top: unit(40/@base-px-2-rem, rem);
    //have some problem
    .swiper-slide {
        &:last-child {
            display: none;
        }
    }
}


/************************

 - 

************************/

section.product-longPic {
    font-size: 0;
    line-height: 0;
    img {
        width: 100%;
    }
}